function sketch(p) {

  var count = 10;
  var lineWeight = 0;
  var strokeColor = 0;
  var bgcolor = 0;
  var strokeColor2 = 0;

  var drawMode = 0;
  var X = 0;
  var Y = 0;

  var tileCountX;
  var tileCountY;
  var interactive = false;

  var canvasSize = p.windowWidth/4;

  p.setup = function() {
    p.createCanvas(canvasSize, canvasSize);
  };

  p.myCustomRedrawAccordingToNewPropsHandler = (props) => {
    if (props.bgcolor) {
      bgcolor = props.bgcolor;
      bgcolor = "#" + bgcolor.slice(2,)
      var c = bgcolor.substring(1);      // strip #
      var rgb = parseInt(c, 16);   // convert rrggbb to decimal
      var r = (rgb >> 16) & 0xff;  // extract red
      var g = (rgb >>  8) & 0xff;  // extract green
      var b = (rgb >>  0) & 0xff;  // extract blue

      var luma = 0.2126 * r + 0.7152 * g + 0.0722 * b; // per ITU-R BT.709

      if (luma < 80) {
          strokeColor2 = 255;
      }
      console.log(luma)
    }
    if (props.params) {
      drawMode = Math.floor(props.params / 100) % 4
      console.log(drawMode)
      X = (Math.floor(props.params / 100) % p.windowWidth/5 + 1)/30 +1;
      Y = (Math.floor(props.params / 10000) % p.windowWidth/5 + 1)/30 +1;
    }
    if (props.interactive) {
      interactive = props.interactive;
      p.resizeCanvas(p.windowHeight - 200, p.windowHeight - 200);
      canvasSize = p.windowHeight - 200;
      }
  }

  p.draw = function() {
    if (!interactive) {
      p.noLoop();
      tileCountX = X;
      tileCountY = Y;
    } else {
      tileCountX = p.mouseX / 30 + 1;
      tileCountY = p.mouseY / 30 + 1; 
    }
    p.background(bgcolor);

    var tileWidth = canvasSize / tileCountX;
    var tileHeight = canvasSize / tileCountY;

    for (var gridY = 0; gridY <= tileCountY; gridY++) {
      for (var gridX = 0; gridX <= tileCountX; gridX++) {
        var posX = tileWidth * gridX;
        var posY = tileHeight * gridY;

        var x1 = tileWidth / 2;
        var y1 = tileHeight / 2;
        var x2 = 0;
        var y2 = 0;

        p.push();
        p.translate(posX, posY);

        for (var side = 0; side < 4; side++) {
          for (var i = 0; i < count; i++) {
            // move end point around the four sides of the tile
            switch (side) {
            case 0:
              x2 += tileWidth / count;
              y2 = 0;
              break;
            case 1:
              x2 = tileWidth;
              y2 += tileHeight / count;
              break;
            case 2:
              x2 -= tileWidth / count;
              y2 = tileHeight;
              break;
            case 3:
              x2 = 0;
              y2 -= tileHeight / count;
              break;
            default:
              // do nothing
            }

            // adjust weight and color of the line
            if (i < count / 2) {
              lineWeight += 1;
              strokeColor += 60;
            } else {
              lineWeight -= 1;
              strokeColor -= 60;
            }

            // set colors depending on draw mode
            switch (drawMode) {
            case 0: 
              p.stroke(strokeColor2);
              break;
            case 1:
              p.stroke(strokeColor2);
              break;
            case 2:
              p.stroke(strokeColor2);
              p.strokeWeight(lineWeight);
              break;
            case 3:
              p.stroke(strokeColor);
              if (!interactive) {
                p.strokeWeight(X / 100);
              } else {
                p.strokeWeight(p.mouseX / 100);
              }
              break;
            default:
              // do nothing
            }

            // draw the line
            p.line(x1, y1, x2, y2);
          }
        }
        p.pop();
      }
    }
  }
};

export default sketch;
