import React from 'react';
import Image from './Image.jsx'


const All = ({ArtworksCount, ArtworksJSON}) => {


    return (
      <div>

        {(ArtworksCount === 0) ? 
          <div className="container ">
            <div className="row g-5">

              <p className="text-light text-center">._. ERROR_NO_ART_YET ._.</p>
            </div>
          </div>
        :
        <div className="container d-flex">
          <div className="row row-cols-auto justify-content-start g-5">
            {ArtworksJSON.map((artwork) => {
                return <div className="col"><Image key={artwork.id} {...artwork}/></div>
            })}

          </div>
        </div>

      }

      </div>
    );
  }

export default All;


