import React, { useState, useEffect } from 'react';
import Web3 from 'web3'

import ArtworkToken from '../abis/ArtworkOwnership.json'
import Navbar from './Navbar'
import Footer from './footer'
import Image from './Image.jsx'


// Hook
const useWindowSize = () => {
  // Initialize state with undefined width/height so server and client renders match
  // Learn more here: https://joshwcomeau.com/react/the-perils-of-rehydration/
  const [windowSize, setWindowSize] = useState({
    width: undefined,
    height: undefined,
  });
  useEffect(() => {
    // Handler to call on window resize
    const handleResize = () => {
      // Set window width/height to state
      setWindowSize({
        width: window.innerWidth,
        height: window.innerHeight,
      });
    }
    // Add event listener
    window.addEventListener("resize", handleResize);
    // Call handler right away so state gets updated with initial window size
    handleResize();
    // Remove event listener on cleanup
    return () => window.removeEventListener("resize", handleResize);
  }, []); // Empty array ensures that effect is only run on mount
  return windowSize;
}

const Search = ({ location }) => {

  const size = useWindowSize();

  // useState hooks index
  const [index, setIndex] = useState('0');
  const [owned, setOwned] = useState(false)
  const [artworkDetails, setArtworkDetails] = useState({})
  const [account, setAccount] = useState('0x0')
  const [loading, setLoading] = useState(true)
  const [artworkToken, SetArtworkToken] = useState()
  const [image, setImage] = useState(<p id="loader" className="text-center">Loading...</p>)
  const [ratingButton, setRatingButton] = useState(<div></div>)
  const [ownerButton, setOwnerButton] = useState(<div></div>)  
  const [rate, setRate] = useState('0')
  const [name, setName] = useState('')
  const [bgcolor, setBgcolor] = useState('0x000000')
  let newColor = '0x000000'

  // equivalent to componentDidMount, fires once when component mounts
  useEffect(() => {
    // get all the URLParams
    const params = new URLSearchParams(location.search);
    // get the q param
    const q = params.get('q');
    // set language in state to the q parameter
    setIndex(q ? q : '0');
    //eslint-disable-next-line
    async function asynFx() {
      await loadWeb3();
      await loadBlockchainData();     
    }

    asynFx()

  }, []);

  const loadWeb3 = async () => {
    if (window.ethereum) {
      window.web3 = new Web3(window.ethereum)
      await window.ethereum.enable()
    }
    else if (window.web3) {
      window.web3 = new Web3(window.web3.currentProvider)
    }
    else {
      window.alert('Non-Ethereum browser detected. You should consider trying MetaMask!')
    }
  }

  const loadBlockchainData = async () => {
    const web3 = window.web3

    const accounts = await web3.eth.getAccounts()
    setAccount(accounts[0])

    const networkId = await web3.eth.net.getId()
 
    // Load ArtworkToken
    const artworkTokenData = ArtworkToken.networks[networkId]
    if(artworkTokenData) {
      const artworkContract = new web3.eth.Contract(ArtworkToken.abi, artworkTokenData.address)
      SetArtworkToken(artworkContract)
    } else {
        window.alert('ArtworkToken contract not deployed to detected network.')
    }
    setLoading(false)
  }

  useEffect(() => {
    console.log(owned)
  }, [index, account, owned, artworkDetails])


  useEffect(() => {

    const getArtworkOwner = async () => {
      const response = await artworkToken.methods.artworkToOwner(index).call()
      console.log(response, account)
      if (response === account) {
        setOwned(true)
      }
    }
    if (typeof artworkToken !== 'undefined'
      && typeof account !== 'undefined'){
      getArtworkOwner()
    }
    
  }, [index, artworkToken, account, loading])

  useEffect(() => {

    if (typeof artworkToken !== 'undefined') {
      getArtworkDetails(index).then((response) => {
      const json = {
        'id': index, 
        'name': response.name, 
          'params': response.params, 
          'bgcolor': response.bgcolor, 
          'rating': response.rating, 
          'raters': response.raters,
          'level': response.level,
          'interactive': true
        }
      // we only care about the json of current id  
      setArtworkDetails(json)
      })
    }
        
  }, [index, artworkToken, ownerButton, loading])

  const getArtworkDetails = (id) => {
    return artworkToken.methods.artworks(id).call()
  }

  useEffect(() => {

    if (artworkDetails === {} || index === 'undefined' || loading === true) {
      setImage(<p id="loader" className="text-center">Loading...</p>)
    } else {
      console.log(artworkDetails)
      setImage(<Image {...artworkDetails}/>)
    }
    
  }, [artworkDetails, size])

  useEffect(() => {
    newColor = "0x" + (Math.random() * 0xffffff).toString(16).slice(0,6)
    newColor = newColor.replace('.', '0')

    setBgcolor(newColor)

  }, [loading])

  const changeArtworkRating = (id, rating) => {
    setLoading(true)
    artworkToken.methods.changeRating(id, rating).send({ from: account })
    .on('receipt', (receipt) => {

      setLoading(false)

      console.log("Successfully rated!")
    })
    .on("error", (error) => {
      setLoading(false)
      console.log(error)
    })

  }

  const changeArtworkNaming = async (id, name) => {
    setLoading(true)
    artworkToken.methods.changeName(id, `${name}`).send({ from: account })
    .on('receipt', (receipt) => {

      setLoading(false)

      console.log("Successfully renamed!")
    })
    .on("error", (error) => {
      setLoading(false)
      console.log(error)
    })
  }

  const changeArtworkParams = async (id) => {
    setLoading(true)
    artworkToken.methods.changeParams(id).send({ from: account })
    .on('receipt', (receipt) => {

      setLoading(false)

      console.log("Successfully reparamized!")
    })
    .on("error", (error) => {
      setLoading(false)
      console.log(error)
    })
  }

  const changeArtworkBgcolor = async (id, bgcolor) => {
    setLoading(true)
    artworkToken.methods.changeBgcolor(id, bgcolor).send({ from: account })
    .on('receipt', (receipt) => {

      setLoading(false)
      console.log("Successfully changed background!")
    })
    .on("error", (error) => {
      setLoading(false)
      console.log(error)
    })
  }

  const changeArtworkLevel = async (id) => {
    setLoading(true)
    artworkToken.methods.levelUp(id).send({ from: account, value: Web3.utils.toWei("0.01", "ether")})
    .on('receipt', (receipt) => {

      setLoading(false)

      console.log("Successfully leveled up!")
    })
    .on("error", (error) => {
      setLoading(false)
      console.log(error)
    })
  }

  useEffect(() => {

    if (typeof ArtworkToken !== 'undefined' && loading === false && !owned) {
      setRatingButton(<form onSubmit={handleRateSubmit}>
                  <label>     
                    <select value={rate} onChange={handleRateChange}>
                      <option value="0">0</option>
                      <option value="1">1</option>
                      <option value="2">2</option>
                      <option value="3">3</option>
                      <option value="4">4</option>
                      <option value="5">5</option>
                      <option value="6">6</option>
                      <option value="7">7</option>
                      <option value="8">8</option>
                      <option value="9">9</option>
                      <option value="10">10</option>
                    </select>
                  </label>
                  <input class="btn btn-outline-light" type="submit" value="Rate" />
                </form>)
    } else {
      setRatingButton(<div></div>)
    }
    
  }, [ArtworkToken, loading, rate, owned])

  useEffect(() => {

    if (typeof ArtworkToken !== 'undefined' && loading === false && owned) {
      setOwnerButton(
        <div>
          <div>
            <form onSubmit={handleNameSubmit}>
              <label>
                <input type="text" value={name} onChange={handleNameChange} />
              </label>
              <input className="btn btn-outline-light" type="submit" value="Rename"/>
            </form>
          </div>
          <div>
            <button className="btn btn-outline-light" onClick={handleParamsButton}>
              New Artwork
            </button>
          </div>
          <div>
            <button className="btn btn-outline-light" onClick={handleBackgroundButton}>
              New Background
            </button>
          </div>
            <button className="btn btn-outline-light" onClick={handleLevelUpButton}>
              Level Up
            </button>
          <div>
          </div>
        </div>
        )
    } else {
      setOwnerButton(<div></div>)
    }
    
  }, [ArtworkToken, loading, owned, name])

  const handleRateChange = (event) => {
    setRate(event.target.value);
    console.log(`rating is changed to ${rate}`)
  }

  const handleRateSubmit = (event) => {
    console.log(`rating is ${rate}`)
    changeArtworkRating(index, rate)
    event.preventDefault();
  }

  const handleNameChange = (event) => {
    setName(event.target.value);
    console.log(`name is changed to ${name}`)
  }

  const handleNameSubmit = (event) => {
    console.log(`naming is ${name}`)
    changeArtworkNaming(index, name)
    event.preventDefault();
  }

  const handleParamsButton = () => {
    changeArtworkParams(index)
  }

  const handleBackgroundButton = () => {
    changeArtworkBgcolor(index, bgcolor)
  }

  const handleLevelUpButton = () => {
    changeArtworkLevel(index)
  }

  return (
    <div>
      <header>
        <Navbar/>
      </header>
      <div className='Content'>
        <div className='container pt-5' style={{ height: '100vh' }}>

          <div className="row">  
            
            <div className="col">{image}</div>

            <div className="col">
              <h5>RATE/SETTINGS</h5>

              {ratingButton}

              {ownerButton}

            </div>

            
          </div>

          
        </div>
      </div>
      <div className="footer">
      <Footer/>
      </div>
    </div>
  );
};

export default Search;