import React, { useState, useEffect } from 'react'
import Web3 from 'web3'
import bootstrap from 'bootstrap'
import Navbar from './Navbar'
import Footer from './footer'
import ArtworkToken from '../abis/ArtworkOwnership.json'
import User from './User.jsx'
import All from './All.jsx'

import BackgroundVideo from './BackgroundVideo/BackgroundVideo';


const Home = () => {
	const [account, setAccount] = useState('0x0')
	const [artworkCount, setArtworkCount] = useState(0)
	const [loading, setLoading] = useState(true)
	const [all, setAll] = useState(<p id="loader" className="text-center">Loading...</p>)
	const [artworksOwned, setArtworksOwned] = useState([])
	const [artworksOwnedDetails, setArtworksOwnedDetails] = useState([])	
	const [artworksAllDetails, setArtworksAllDetails] = useState([])	
	const [artworkToken, SetArtworkToken] = useState()
	const [loader, setLoader] = useState(<p id="loader" className="text-center">Loading...</p>)

	useEffect(() => {
		async function asyncFx() {
      		await loadWeb3();
      		await loadBlockchainData(); 		
      	}

      	asyncFx()

	}, [])

	useEffect(() => {
		async function asyncFx() {
			const web3 = window.web3
			const accounts = await web3.eth.getAccounts()
	    	setAccount(accounts[0])
		}

		window.ethereum.on('accountsChanged', function (accounts) {
  			// Time to reload your interface with accounts[0]!
  			console.log('account update')
  			asyncFx();
		})
	})

 
    const loadWeb3 = async () => {
		if (window.ethereum) {
		  window.web3 = new Web3(window.ethereum)
		  await window.ethereum.enable()
		}
		else if (window.web3) {
		  window.web3 = new Web3(window.web3.currentProvider)
		}
		else {
		  window.alert('Non-Ethereum browser detected. You should consider trying MetaMask!')
		}
	}

    const loadBlockchainData = async () => {
	    const web3 = window.web3

	    const accounts = await web3.eth.getAccounts()
	    setAccount(accounts[0])

	    const networkId = await web3.eth.net.getId()
	 
	    // Load ArtworkToken
	    const artworkTokenData = ArtworkToken.networks[networkId]
	    if(artworkTokenData) {
	    	const artworkContract = new web3.eth.Contract(ArtworkToken.abi, artworkTokenData.address)
	    	SetArtworkToken(artworkContract)
	    } else {
	        window.alert('ArtworkToken contract not deployed to detected network.')
	    }

	    // const artworksOwnedArray = await artworkToken.methods.getArtworksByOwner(account).call()
	    // setArtworksOwned(artworksOwnedArray)  
	// 
	//     // Load DappToken
	//     const dappTokenData = DappToken.networks[networkId]
	//     if(dappTokenData) {
	//       const dappToken = new web3.eth.Contract(DappToken.abi, dappTokenData.address)
	//       this.setState({ dappToken })
	//       let dappTokenBalance = await dappToken.methods.balanceOf(this.state.account).call()
	//       this.setState({ dappTokenBalance: dappTokenBalance.toString() })
	//     } else {
	//       window.alert('DappToken contract not deployed to detected network.')
	//     }
	// 
	//     // Load TokenFarm
	//     const tokenFarmData = TokenFarm.networks[networkId]
	//     if(tokenFarmData) {
	//       const tokenFarm = new web3.eth.Contract(TokenFarm.abi, tokenFarmData.address)
	//       this.setState({ tokenFarm })
	//       let stakingBalance = await tokenFarm.methods.stakingBalance(this.state.account).call()
	//       this.setState({ stakingBalance: stakingBalance.toString() })
	//     } else {
	//       window.alert('TokenFarm contract not deployed to detected network.')
	//     }
	    setLoading(false)
	}

	useEffect(() => {

		const getArtworksByOwner = async (owner) => {
		const response = await artworkToken.methods.getArtworksByOwner(owner).call()
		setArtworksOwned(response)
		console.log(response)

	}
		if (typeof artworkToken !== 'undefined'
			&& typeof account !== 'undefined'){
			getArtworksByOwner(account)
		}
		
	}, [artworkToken, account])

	useEffect(() => {

		if (loading) {
			setLoader(<p id="loader" className="text-center">Loading...</p>)
		} else {
			setLoader(<User ArtworksJSON={artworksOwnedDetails} Artworks={artworksOwned} Button={createArtwork}/>)
		}
		
	}, [loading, artworksOwned, artworksOwnedDetails])


	useEffect(() => {

		setArtworksOwnedDetails([])
		artworksOwned.map((id) => (
			getArtworkDetails(id).then((response) => {
				const json = {
					'id': id, 
					'name': response.name, 
		     		'params': response.params, 
		     		'bgcolor': response.bgcolor, 
		     		'rating': response.rating, 
		     		'raters': response.raters,
		     		'owner': response.owner,
		     		'level': response.level,
		     		'interactive': false
		     	}
				setArtworksOwnedDetails((state) => [...state, json])
			})
		))
		
	}, [artworksOwned])



	const createArtwork = () => {
		setLoading(true)
		artworkToken.methods.createRandomArtwork().send({ from: account })
		.on('receipt', (receipt) => {

		  setLoading(false)

		  getArtworksByOwner(account)
		  .then((response) => {
		  	setArtworksOwned(response)
		  })


		  console.log("Successfully created!")
		})
		.on("error", (error) => {
		setLoading(false)
	  	console.log(error)
		})
	}

	const getArtworksByOwner = (owner) => {
		return artworkToken.methods.getArtworksByOwner(owner).call()
	}	  

	const getArtworkDetails = (id) => {
        return artworkToken.methods.artworks(id).call()
      }

	const getArtworkCount = () => {
        return artworkToken.methods.getArtworkCount().call()
      }



    // artwork counts will be updated here
    useEffect(() => {
    	artworkToken && (getArtworkCount().then((response) => {
		  	setArtworkCount(response)
		  }))
	},[loading])

// 	useEffect(() => {
// 
// 		for (let i=0; i<artworkCount; i++) {
// 			getArtworkDetails(i).then((response) => {
// 				const json = {
// 					'id': i, 
// 					'name': response.name, 
// 		     		'params': response.params, 
// 		     		'bgcolor': response.bgcolor, 
// 		     		'rating': response.rating, 
// 		     		'raters': response.raters
// 		     	}
// 		     	setArtworksAllDetails((state) => [...state, json])
// 		     }
// 		 }
// 		}, [artworkCount])

	useEffect(() => {

		setAll(<All ArtworksJSON={artworksAllDetails} ArtworksCount={artworkCount} Button={createArtwork}/>)
		
	}, [artworksAllDetails])

	useEffect(() => {
		setArtworksAllDetails([])
		for (let i=0; i<artworkCount; i++) {
			getArtworkDetails(i).then((response) => {
 				const json = {
 					'id': i, 
 					'name': response.name, 
 		     		'params': response.params, 
 		     		'bgcolor': response.bgcolor, 
 		     		'rating': response.rating, 
 		     		'raters': response.raters,
 		     		'owner': response.owner,
 		     		'level': response.level,
 		     		'interactive': false
 		     	}
 		     	setArtworksAllDetails((state) => [...state, json])

			})

		}
	}, [artworkCount])

 	useEffect(() => {
 		console.log(`the account: ${account}`)
 		console.log(`the artworks owned: ${artworksOwned}`)
 		console.log(`the artworks owned details:`)
 		console.log(artworksOwnedDetails)
 		console.log(`all artworks count: ${artworkCount}`)
 		console.log(`artwork details:`)
 		console.log(artworksAllDetails)
 		
 	}, [artworksOwned, artworksOwnedDetails, artworkCount, artworksAllDetails, all])


	return (				
      <div>
      	<header>
      		<Navbar/>
      		
      		<div className="video-container"><BackgroundVideo /></div>

      	</header>
        

        {all}
        <div className="footer">
	        <Footer/>
	    </div>

      </div>	

	)
}

export default Home;

