function sketch(p) {

  var lissajousPoints = [];
  var freqX = 4;
  var freqY = 7;
  var phi = 15;
  var phiRand = 0;
  var phiSub = 1;

  var modFreqX = 3;
  var modFreqY = 2;

  var lineWeight = 0.1;
  var lineColor;
  var lineAlpha = 50;

  var connectionRadius = 100;

  var bgcolor = '#ffffff'
  var interactive = false;

  var first = true;

  var canvasSize = p.windowWidth/4;
  var pointCount = canvasSize*2;

  p.setup = function() {
    p.createCanvas(canvasSize,canvasSize);
    p.colorMode(p.RGB, 255, 255, 255, 100);
    p.noFill();

    lineColor = p.color(0, 50);

    // calculateLissajousPoints();
    // drawLissajous();
  };

  p.myCustomRedrawAccordingToNewPropsHandler = (props) => {
    if (props.bgcolor) {
      bgcolor = props.bgcolor;
      bgcolor = "#" + bgcolor.slice(2,)
      var c = bgcolor.substring(1);      // strip #
      var rgb = parseInt(c, 16);   // convert rrggbb to decimal
      var r = (rgb >> 16) & 0xff;  // extract red
      var g = (rgb >>  8) & 0xff;  // extract green
      var b = (rgb >>  0) & 0xff;  // extract blue

      var luma = 0.2126 * r + 0.7152 * g + 0.0722 * b; // per ITU-R BT.709

      if (luma < 80) {
          lineColor = p.color(255, 50);
      }
      console.log(luma)
    }
    if (props.interactive) {
      interactive = props.interactive;
      p.resizeCanvas(p.windowHeight - 200, p.windowHeight - 200);
      canvasSize = p.windowHeight - 200;
    }

    if (props.params) {
      freqX = (Math.floor(props.params / 1000) % 25) + 1
      freqY = (Math.floor(props.params / 10000) % 25) + 1
      modFreqX = (Math.floor(props.params / 100000) % 25) + 1
      modFreqY = (Math.floor(props.params / 1000000) % 25) + 1
      phiRand = (Math.floor(props.params / 10000000) % 15) + 1
      // 1 or -1
      phiSub = (2 * (Math.floor(props.params / 100000000) % 1)) - 1

      freqX = p.max(freqX, 1)
      freqX = p.max(freqY, 1)
      modFreqX = p.max(modFreqX,1);
      modFreqY = p.max(modFreqY,1);

      phi = phiRand * phiSub * 15
      }

  }

  p.draw = function() {
    if (first) {
      p.background(bgcolor);
      calculateLissajousPoints();
      drawLissajous(); 
      first = false;
    }
    if (!interactive) {
      p.noLoop();

    } else {
    }
    var _phi = p.map(p.mouseX, (canvasSize/10), canvasSize - (canvasSize/10), -(3*canvasSize/10), (3*canvasSize/10));
    if (p.abs(phi - _phi) > 30) {
      phi = _phi
      p.background(bgcolor);
      calculateLissajousPoints();
      drawLissajous(); 
    }
  }

  function calculateLissajousPoints() {
    for (var i = 0; i <= pointCount; i++) {
      var angle = p.map(i, 0, pointCount, 0, p.TAU);

      var x = p.sin(angle * freqX + p.radians(phi)) * p.cos(angle * modFreqX);
      var y = p.sin(angle * freqY) * p.cos(angle * modFreqY);
      x *= p.width / 2 - 30;
      y *= p.height / 2 - 30;

      lissajousPoints[i] = p.createVector(x,y);
    }
  }

  function drawLissajous() {
    p.strokeWeight(lineWeight);
    p.push();
    p.translate(p.width / 2, p.height / 2);

    for (var i1 = 0; i1 < pointCount; i1++) {
      for (var i2 = 0; i2 < i1; i2++) {
        var d = lissajousPoints[i1].dist(lissajousPoints[i2]);
        var a = p.pow(1 / (d / connectionRadius + 1), 6);
        if (d <= connectionRadius) {
          p.stroke(lineColor, a * lineAlpha);
          p.line(
            lissajousPoints[i1].x,
            lissajousPoints[i1].y,
            lissajousPoints[i2].x,
            lissajousPoints[i2].y
          );
        }
      }
    }
    p.pop();
  }
};

export default sketch;