function sketch(p) {


  var joinedText;
  var alphabet = 'ABCDEFGHIJKLMNOPQRSTUVWYZÄÖÜß,.;!? ';
  var counters = [];

  var posX;
  var posY;

  var bgcolor = '0x000000';
  var interactive = false;
  var r = 0;
  var g = 0;
  var b = 0;

  var canvasSize = p.windowWidth/4;

  p.setup = function() {
    p.createCanvas(canvasSize, canvasSize);

    joinedText = "qwerrttttzuiooopüää???äölkjhhhhhhhgfdsayyxcvbbnm,...?!"


    p.noStroke();
    p.textFont('monospace', 18);

    // use the following command, to collect all characters in the text automatically
    // alphabet = getUniqCharacters();

    for (var i = 0; i < alphabet.length; i++) {
      counters[i] = 0;
  }

  countCharacters();

    };

  p.myCustomRedrawAccordingToNewPropsHandler = (props) => {

    if (props.params && props.bgcolor) {
      var c = bgcolor.substring(1);      // strip #
      var _rgb = parseInt(c, 16);   // convert rrggbb to decimal
      var _r = (_rgb >> 16) & 0xff;  // extract red
      var _g = (_rgb >>  8) & 0xff;  // extract green
      var _b = (_rgb >>  0) & 0xff;  // extract blue

      var _luma = 0.2126 * _r + 0.7152 * _g + 0.0722 * _b; // per ITU-R BT.709

      r = Math.floor(props.params / 100) % 255;
      g = Math.floor(props.params / 1000) % 255;
      b = Math.floor(props.params / 10000) % 255;

      console.log(_luma)

      var luma = 0.2126 * r + 0.7152 * g + 0.0722 * b; // per ITU-R BT.709

      if (p.abs(luma - _luma) < 80) {
          r = p.abs(r-255);
          g = p.abs(g-255);
          b = p.abs(b-255);
      }
    }

    if (props.bgcolor) {
      bgcolor = props.bgcolor;
      bgcolor = "#" + bgcolor.slice(2,)
    }

    if (props.interactive) {
      interactive = props.interactive;
      p.resizeCanvas(p.windowHeight - 200, p.windowHeight - 200);
      canvasSize = p.windowHeight - 200;
    }
  }

  p.draw = function() {
    if (!interactive) {
      p.noLoop();

    } else {
    }
    p.background(bgcolor);

    posX = 20;
    posY = 40;

    // go through all characters in the text to draw them
    for (var i = 0; i < joinedText.length; i++) {
      // again, find the index of the current letter in the character set
      var upperCaseChar = joinedText.charAt(i).toUpperCase();
      var index = alphabet.indexOf(upperCaseChar);
      if (index < 0) continue;

      p.fill(r, g, b, counters[index] * 30);

      var sortY = index * 20 + 40;
      var m = p.map(p.mouseX, 50, canvasSize - 50, 0, 1);
      m = p.constrain(m, 0, 1);
      var interY = p.lerp(posY, sortY, m);

      p.text(joinedText.charAt(i), posX, interY);

      posX += p.textWidth(joinedText.charAt(i));
      if (posX >= canvasSize - 200 && upperCaseChar === ' ') {
        posY += 30;
        posX = 20;
      }
    }
  }

  function countCharacters() {
    for (var i = 0; i < joinedText.length; i++) {
      // get one character from the text and turn it to uppercase
      var c = joinedText.charAt(i);
      var upperCaseChar = c.toUpperCase();
      var index = alphabet.indexOf(upperCaseChar);
      // increase the respective counter
      if (index >= 0) counters[index]++;
    }
  }

//   function getUniqCharacters() {
//     var charsArray = joinedText.toUpperCase().split('');
//     var uniqCharsArray = charsArray.filter(function(char, index) {
//       return charsArray.indexOf(char) === index;
//     }).sort();
//     return uniqCharsArray.join('');
//   }

}


export default sketch;