function centerGradient(x1, y1, r1, x2, y2, r2, c1, c2, drawingContext) {
	var ctx = drawingContext; // global canvas context p5.js var
	var cx = x1 + (x2 - x1) / 2;
	var cy = y1 + (y2 - y1) / 2;
	var grd = ctx.createRadialGradient(cx, cy, r1, cx, cy, r2);
	grd.addColorStop(0, c1.toString());
	grd.addColorStop(1, c2.toString());
	ctx.fillStyle = grd;
	ctx.fillRect(x1, y1, x2 - x1, y2 - y1);
}


function sketch(p) {

	var colorCount = 20;
	var hueValues = [];
	var saturationValues = [];
	var brightnessValues = [];
	var actRandomSeed = 0;
	var alphaValue = 100;

	var canvasSize = p.windowWidth/4;

	let bgcolor = '#ffffff';

	p.setup = function() {
		console.log(p.windowHeight)
		p.createCanvas(canvasSize, canvasSize);
		p.colorMode(p.HSB, 360, 100, 100, 100);
		p.noStroke();
	};

    p.myCustomRedrawAccordingToNewPropsHandler = (props) => {
        if (props.bgcolor) {
            bgcolor = props.bgcolor;
            bgcolor = "#" + bgcolor.slice(2,)
        }

        if (props.params) {
        	actRandomSeed = props.params;
        }

        if (props.interactive){
        	p.resizeCanvas(p.windowHeight - 200, p.windowHeight - 200); 
        }
    }

	p.draw = function() {
		p.noLoop();
		p.background(bgcolor);
		p.randomSeed(actRandomSeed);

		for (var j = 0; j < colorCount; j++) {
		    if (j % 2 === 0) {
				hueValues[j] = p.random(180);
				saturationValues[j] = p.random(50);
				brightnessValues[j] = 100;
		    } else {
				hueValues[j] = p.random(360);
				saturationValues[j] = 100;
				brightnessValues[j] = p.random(100);
		    }
	  	}

		// ------ area tiling ------
		// count tiles
		var counter = 0;
		// row count and row height
		var rowCount = p.int(p.random(5, 30));
		var rowHeight = p.height / rowCount;

		// seperate each line in parts
		for (var i = rowCount; i >= 0; i--) {
			// how many fragments
			var partCount = i + 1;
			var parts = [];

			for (var ii = 0; ii < partCount; ii++) {
				// sub fragments or not?
				if (p.random() < 0.075) {
				    // take care of big values
				    var fragments = p.int(p.random(2, 20));
				    partCount = partCount + fragments;
				    for (var iii = 0; iii < fragments; iii++) {
				      parts.push(p.random(2));
				    }
			  	} else {
			    	parts.push(p.random(2, 20));
			  	}
			}

			// add all subparts
			var sumPartsTotal = 0;
			for (var jj = 0; jj < partCount; jj++) {
				sumPartsTotal += parts[jj];
			}

			// draw rects
			var sumPartsNow = 0;
			for (var jjj = 0; jjj < parts.length; jjj++) {
				sumPartsNow += parts[jjj];

				if (p.random() < 0.45) {
				    var w = p.map(parts[jjj], 0, sumPartsTotal, 0, p.width);
				    var h = rowHeight * 1.5;
				    var px1 = p.map(sumPartsNow, 0, sumPartsTotal, 0, p.width);
				    var px2 = px1 + w;
				    var py1 = rowHeight * i;
				    var py2 = py1 + h;

				    var index = counter % colorCount;
				    var col1 = p.color(hueValues[index], saturationValues[index], brightnessValues[index], alphaValue);
				    // create complementary color
				    var col2 = p.color(hueValues[index] - 180, saturationValues[index], brightnessValues[index], alphaValue);
				    centerGradient(px1, py1, 0, px2, py2, p.max(w, h), col1, col2, p.drawingContext);
				}

				counter++;
			}
		}

 
	};
};

export default sketch;