import invert from 'invert-color';

function sketch(p) {

  var drawMode = 1;
  var x;
  var s;
  var a;
  var bgcolor = "#ffffff";
  var interactive = false;
  var canvasSize = p.windowWidth/4;

  p.setup = function() {
      p.createCanvas(canvasSize, canvasSize);
      p.noFill();
    };

  p.myCustomRedrawAccordingToNewPropsHandler = (props) => {
    if (props.bgcolor) {
        bgcolor = props.bgcolor;
        bgcolor = "#" + bgcolor.slice(2,)
    }

    if (props.params) {
      x = p.map((Math.floor(props.params / 100) % canvasSize), 0, canvasSize, -(canvasSize/10), (canvasSize/10));
      a = p.map((Math.floor(props.params / 100) % canvasSize), 0, canvasSize, -0.5, 0.5);
      s = p.map((Math.floor(props.params / 10000) % canvasSize), 0, canvasSize, 0.7, 1);
      drawMode = Math.floor(props.params / 100) % 2 + 1
    }

    if (props.interactive) {
      interactive = props.interactive;
      p.resizeCanvas(p.windowHeight - 200, p.windowHeight - 200);
      canvasSize = p.windowHeight - 200;
    }
  }

  p.draw = function() {
    if (!interactive) {
      p.noLoop();
    } else {
      // second shape (dynamically translated/rotated and scaled)
      x = p.map(p.mouseX, 0, canvasSize, -50, 50);
      a = p.map(p.mouseX, 0, canvasSize, -0.5, 0.5);
      s = p.map(p.mouseY, 0, canvasSize, 0.7, 1);
    }
    p.background(bgcolor);

    p.translate(canvasSize / 2, canvasSize / 2);

    // first shape (fixed)
    p.stroke(invert(bgcolor));
    p.strokeWeight(3);
    overlay();

    if (drawMode === 1) p.rotate(a);
    if (drawMode === 2) p.translate(x, 0);
    p.scale(s);

    p.stroke(invert(bgcolor));
    p.strokeWeight(2);
    overlay();
  }

  function overlay() {
    var w = canvasSize - (canvasSize/5);
    var h = canvasSize - (canvasSize/5);

    if (drawMode === 1) {
      for (var i = -w / 2; i < w / 2; i += 5) {
        p.line(i, -h / 2, i, h / 2);
      }
    } else if (drawMode === 2) {
      for (var ii = 0; ii < w; ii += 10) {
        p.ellipse(0, 0, ii);
      }
    }
  }
}


export default sketch;